<template>
    <div class="shopcart">
        <div v-if='isLogin'>
            <div v-if="shopcartData.length == 0">
                <van-empty description="购物车空空如也，逛一逛" />
            </div>
            <div class="shopcart-top">
                <van-nav-bar
                title="购物车"
                :right-text="isToggle?'管理':'完成'" 
                :fixed="true"
                @click-right='isToggle = !isToggle'
                />
            </div>
            <div class="shop-list">
                <van-swipe-cell class="shop-list-all clearfix" v-for='(item,index) in shopcartData' :key='index' >
                    <van-checkbox :name='item.sid' v-model='item.ischeck'  class="list-chek fl"  @click='checkOne(item)'/>
                    <div class="shop-list-one">

                        <img :src="item.small_img" alt="" class="fl">
                        <div class="fl">
                            <div class="list-one-top">
                                <span>{{item.name}}</span><span class="one-text">{{item.rule}}</span>
                            </div>
                            <div class="list-one-center">{{item.enname}}</div>
                            <div class="list-one-bottom clearfix" >
                                <div class="fl">￥{{item.price}}</div><div class="fr"><van-stepper v-model="item.count" theme="round" button-size="30" disable-input @change='changeprice(item)' /></div>
                            </div>
                        </div>
                        
                    </div>

                    <template #right>
                        <van-button square text="删除" type="danger" class="delete-button" @click='removeShopcart([item.sid], index)' />
                    </template>
                </van-swipe-cell>
                <div v-if="isToggle">
                    <van-submit-bar class="submit-bar" :price="allprice"  button-text="提交订单" button-type="info" @submit='toaddress'>
                        <van-checkbox v-model="isAll" @click="checkAll" :disabled='shopcartData.length == 0'>全选</van-checkbox>
                    </van-submit-bar>
                </div>
                <div v-else>
                    <van-submit-bar  button-text="删除选择"  :price="allprice" @submit="allremoveshopcart">
                        <van-checkbox class="fl" v-model="isAll" @click="checkAll" :disabled='shopcartData.length == 0'>全选</van-checkbox>
                    </van-submit-bar>
                </div>
            </div>
        </div>
        <div v-else>
            <van-empty description="" />
                <div class="login-null">请先<span @click="toLogin">登入</span></div>
        </div>
    </div>
</template>

<script>
import "../../assets/css/shopcart.less"
    export default {
        data(){
            return{
                isLogin:false,
                a:true,
                allcheck:false,
                count:0,
                result: [],
                isAll:false,
                isToggle:true,
                shopcartData:[],
                allprice:0,
                num:0
            }
        },
        created(){
            if(localStorage.getItem('login_Tk')){
                this.isLogin=true
                this.getShopcartData();
            }
            
        },
        methods:{
            toLogin(){
                this.$router.push({name:'Login'})
            },
            toaddress(){
                this.num = 0
                for(let i = 0 ; i < this.shopcartData.length ; i ++){
                    if(this.shopcartData[i].ischeck){
                        this.num++;
                    }
                }
                if(this.num == 0){
                    this.$toast("请选择商品")
                    return;
                }
                let sids = [];
                this.shopcartData.forEach(v=>{
                    if(v.ischeck){
                        sids.push(v.sid)
                    }
                })
                // 
                this.$router.push({name:'Pay',query:{sids:sids.join('-')}})
            },
            //单个删除购物车商品
            removeShopcart(sids, index) {
                
                
                //获取购物车的sid
                let tokenString = localStorage.getItem('login_Tk');

                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: 'POST',
                    url: '/removeShopcart',
                    data: {
                    appkey: this.appkey,
                    tokenString,

                    //sid购物车唯一标识集合字符串
                    sids: JSON.stringify(sids)
                    }
                }).then(result => {
                    this.$toast.clear();

                    if (result.data.code == 7000) {
                    this.shopcartData.splice(index, 1);
                    if (this.shopcartData.length == 0) {
                        this.loadOptions.finishedText = '';
                    }
                    this.sum();
                    this.$toast.success('删除成功');
                    } else {
                    this.$toast.success('删除失败');
                    }
                    
                }).catch(err => {
                    this.$toast.clear();
                    
                })
            },
            //多个删除
            allremoveshopcart(){
                
                let sids = [];
                this.shopcartData.forEach(v=>{
                    if(v.ischeck){
                        sids.push(v.sid);
                    }
                })
                
                let tokenString = localStorage.getItem('login_Tk');

                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })
                this.axios({
                    method:'POST',
                    url:'/removeShopcart',
                    data:{
                        appkey: this.appkey,
                        tokenString,

                        //sid购物车唯一标识集合字符串
                        sids: JSON.stringify(sids)
                    }
                }).then(result=>{
                    this.$toast.clear();
                    if(result.data.code == 7000){
                        for(let i = this.shopcartData.length-1  ; i >= 0 ; i -- ){
                            if(this.shopcartData[i].ischeck){
                                
                                this.shopcartData.splice(i,1)
                            }
                        }
                    }
                    this.sum()
                    
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            //全选
            checkAll() {
                if(this.isAll){
                    for(let i = 0 ; i < this.shopcartData.length;i++ ){
                        this.shopcartData[i].ischeck = true
                    }
                }else{
                    for(let i = 0 ; i < this.shopcartData.length;i++ ){
                        this.shopcartData[i].ischeck = false
                    }
                }
                
                
                this.sum()
            },
            //单选
            checkOne(item){
                this.sum()
                for(let i = 0 ; i < this.shopcartData.length ; i ++){
                    if(!this.shopcartData[i].ischeck){
                        this.isAll = false 
                        return;
                    }
                }
                this.isAll = true;
                
            },
            //获取商品数据
            getShopcartData(){
                let tokenString = localStorage.getItem('login_Tk');
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    method:'GET',
                    url:'/findAllShopcart',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then(result=>{
                    this.$toast.clear();
                    if(result.data.code==5000){
                        result.data.result.forEach(v=>{
                            v.ischeck = false
                        })
                        this.shopcartData = result.data.result
                    }
                    
                }).catch(err=>{
                    
                })
            },
            //修改商品数量改变价格
            changeprice(item){
                let tokenString = localStorage.getItem('login_Tk');
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    method:"POST",
                    url:'/modifyShopcartCount',
                    data:{
                        appkey: this.appkey,
                        tokenString,
                        sid: item.sid,
                        count: item.count
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    if(result.data.code == 6000 ){
                        this.sum();
                    }
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
                
            },
            sum(){
                this.allprice = 0
                this.shopcartData.forEach(v=>{
                    if(v.ischeck){
                        this.allprice += v.price*v.count*100
                    }
                })
            }
        }
    }
</script>

<style lang="less" scoped>
.login-null{
    font-size: 16px;
    text-align: center;
    >span{
        color: skyblue;
    }
}

/deep/ .van-submit-bar {
    bottom: 46px;
    border-bottom: #ddd;
}
/deep/.van-button--info{
    background-color: #f8f0b1;
}
/deep/.van-checkbox__icon{
    background-color: #fff;
    border-radius: 50%;
}
/deep/.van-submit-bar{
    background-color: #f4b973;
}
/deep/.van-submit-bar__button{
     color: #593513;
}
</style>